import "babel-polyfill";
import Vue from 'vue';
import Vuetify from 'vuetify';
import MultiLanguage from 'vue-multilanguage';
import VeeValidate from 'vee-validate';

Vue.use(VeeValidate);

Vue.use(MultiLanguage,
    {
        default: 'ru',
        ru: {
            pset: '.',
            required: ' ',
            toStart: '    , ,      (    )  ',
            resetIt: '         , , ',
            resetItLink: ' ',
            email: 'Email',
            password: '',
            login: '',
            startRegistration: '',
            dataError: '  ',
            loginPasswordError: 'Email    ',
            toSetPass: '  , ,    ,     ',
            toSetTitle: ' ?',
            toSet: ',      (    ),   ,          ',
            receiveInsructions: '           Aurora Prize Guest Management, , ,          .        , , ,  CRM-    : ',
            receiveInstructions: '',
            receiveInsructions2: '',
            close: '',
            sendEmailTitle: ' ',
            sendEmail: '        : {email}.',
            errorTitle: '',
            guestNotFound: '     {val}  .',
            guestRegistred: '      {val}. ,        .',
            emailError: '   ',
            error: '  '
        }
    });


Vue.use(Vuetify);

new Vue({
    el: '#app-account',
    render: function (h) {
        return h(require('./components/Account/Account.vue.html'));
    }
});



